@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap");

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

body {
	background-color: rgb(227, 230, 241);
}

.movie {
	display: flex;
	width: 600px;
	height: 100%;
	/* border: 3px solid black; */
	padding: 10px;
	background-color: #ffffff;
	margin: 35px 10px 10px 10px;
	box-shadow: 1px 2px 13px 1px #8e8e8e;
	border-radius: 5px;
}

.movie * {
	margin-bottom: 5px;
}
.movie img {
	max-width: 180px;
	max-height: 300px;
	object-fit: cover;
	position: relative;
	top: -30px;
	margin: 0px 20px;
	box-shadow: 1px 2px 13px 1px #8e8e8e;
	border-radius: 5px;
}

.movie__data {
	padding: 10px;
}
.movie__title {
	font-size: 1.6rem;
	font-family: "Roboto Condensed", sans-serif;
}
.movie__year {
	font-weight: 300;
	color: rgb(128, 128, 128);
}

.genres {
	display: flex;
	flex-wrap: wrap;
	font-size: 0.8rem;
	color: rgb(65, 142, 145);
}

li {
	list-style: none;
	margin-right: 10px;
}

.movie__summary {
	line-height: 1.5;
	font-size: 0.8rem;
	color: rgb(126, 125, 125);
}
