*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

body {
	background-color: rgb(227, 230, 241);
}

.loader {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.loader__text {
	font-size: 3rem;
	font-weight: 700;
}

.circle {
	width: 3rem;
	height: 3rem;
	background-color: rgb(142, 204, 204);
	border-radius: 50%;
	position: relative;
	right: 140px;
	z-index: -1;
	animation: circleBig 1s linear;
}

.movies {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

@keyframes circleBig {
	0% {
		transform: scale(0.5);
		opacity: 0.5;
	}
	25% {
		transform: scale(3);
		opacity: 1;
	}
	50% {
		transform: scale(5);
		opacity: 0.5;
	}
	75% {
		transform: scale(7);
		opacity: 0.2;
	}
	100% {
		transform: scale(9);
		opacity: 0.1;
	}
}
